"use client"

import { Button } from "@/components/ui/button"
import { LayoutGrid, Rocket } from "lucide-react"
import { cn } from "@/lib/utils"

type Mode = "multi-chat" | "super-fiesta"

interface ModeToggleProps {
  mode: Mode
  onModeChange: (mode: Mode) => void
}

export function ModeToggle({ mode, onModeChange }: ModeToggleProps) {
  return (
    <div className="flex items-center gap-2 p-1 bg-muted rounded-lg">
      <Button
        variant={mode === "multi-chat" ? "default" : "ghost"}
        size="sm"
        onClick={() => onModeChange("multi-chat")}
        className={cn(
          "flex items-center gap-2",
          mode === "multi-chat" && "bg-background shadow-sm"
        )}
      >
        <LayoutGrid className="w-4 h-4" />
        <span>Multi-Chat</span>
      </Button>
      <Button
        variant={mode === "super-fiesta" ? "default" : "ghost"}
        size="sm"
        onClick={() => onModeChange("super-fiesta")}
        className={cn(
          "flex items-center gap-2",
          mode === "super-fiesta" && "bg-background shadow-sm"
        )}
      >
        <Rocket className="w-4 h-4" />
        <span>Super Fiesta</span>
      </Button>
    </div>
  )
}

